	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Отличное упражнение для нагрузки мышц спины. Является аналогом тяги штанги в наклоне, но за счет того, что снаряд не гуляет в стороны, выполнять тягу т-грифа проще. Это позволяет взять больший вес, чем в тяге штанги в наклоне. Упражнение выполняется как в специальном тренажёре, так и со штангой (в этом случае рекомендуется упереть штангу в угол).</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n<li>Исходное положение – наклонитесь вперед практически параллельно полу. Слегка согните колени. Спину держите прямо. Возьмите рукоять тренажера. Если выполняете с грифом, то возьмите гриф сразу под дисками.</li>\n<li>На выдохе – тяните штангу на себя усилием мышц спины.</li>\n<li>На вдохе – верните штангу в исходное положение.</li>\n<li>Выполните тягу необходимое число повторов.</li>\n</ol>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/KDEl3AmZbVE\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/tyaga-t-grifa-v-naklone","efficiency":"6","title":"Тяга Т-грифа в наклоне","image":"/static/2-04-0.png","image_2":"","image_3":"","image_4":"","filterby":"back","date":"April 12, 2017","injury":"8","bodypart":"Спина","filtertype":"base","type":"Базовое","filterinventory":"stange","inventory":"Штанга","filterplace":null}}},"pathContext":{}}

